/* 0. Reset ================================= */
@import 'reset';
@import '_variables'; 

/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

body {
	background: #E7D3C4;
}


/* 2. Content ================================= */

header{

	width: 100%;
	height: 100px;
	position:absolute;
		
	h1{
		color: $white-color;
		text-decoration: underline;
		font-style: uppercase;
	}

}

nav{


	ul{
		display: flex;
		flex-direction: column;

		@media #{$tablet}{
		display: flex;
		flex-direction: row;
		}
	}

		li{
		margin-bottom: 20px;
		margin-top: 20px;

		@media #{$tablet}{
		margin: 20px 10px 20px 10px;
		}
	}

			a{
			color: $white-color;
			font-family: $Zilla;
			font-weight: medium;
			font-size: 1.2rem;
			text-decoration: none;
			border-bottom: 1px solid white;
			}
}


h2{
	color: $white-color;
	font-family: $Monserrat;
	font-size: 1.5rem;
	
}

button{
	background-color: $button-color;
	color: $white-color;
	padding: 25px 25px;
	width: auto;
	min-width: 200px;
	border-radius: 14px;
	border: 2px solid #A65200;
	position: absolute;
    top: 65%;
    font-size: 1rem;
}


// First Page //

.main-header{
	height: 220px;
    display: flex;
    align-items: center;
    flex-direction: column;
    z-index: 1;
    background: $grey-color;
	background-color: rgba(38, 38, 38, 0.9);

	img{
		margin-top: 10px;
	}
	
	@media #{$tablet}{
		align-items: none;
		flex-direction: row;
		justify-content: space-between;
		padding: 40px;
		height: 100px;
	}
}


.banner{
	background-image: url("../img/lee2.jpg");
	background-size: cover;
	position: relative;
	width: 100%;
    height: 550px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;

    h2{
	position:absolute;
	top: 60%;
	margin: 10px; //necessary??
    }

    button{
    	top: 80%;
    }

    @media #{$tablet}{
		background-position: center;
	}
}

.our-story-div{
	display: flex;
	background-color:$tan-color;
}

.our-story{ // space on the bottom?

	height: 100%;

	@media #{$tablet}{
		width: 55%;
	}

	@media #{$desktop-small}{
		width: 60%;
	}

	@media #{$desktop-large}{
		width: 70%;
	}

	h1{
		text-align: center;
		font-family: $Zilla;
		color: $grey-color;
		font-weight: medium;
		font-size: 2rem;
		margin: 25px 20px;
	}

	p{
		line-height: 1.5rem;
		font-family: $Monserrat;
		color: #7a736f;
		margin: 8%;
	}
}


.story-image{

	img{
		display: none;

	@media #{$tablet}{
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
		}	
	}
}


//Drinks Menu Page Styles//


.drink-menu{
	width: 100%;
	display: flex;
	flex-direction: column;
	background-color: $tan-color;

	span.amount{
		float: right;
	}

	
	@media #{$desktop-small}{
		flex-direction: row;
		justify-content: space-around;
	    padding: 20px;
	}
}

.drink-image{
	width: 100%;

	@media #{$tablet}{
		width: 100%;
	}

	@media #{$desktop-small}{
		width: 100%;
		margin-bottom: 5%;
	}

	img{
		width: 100%
	}

}


.drink-banner{
	height: 330px;
	background-size:cover;
	position:relative;
	background-image: url("../img/beans.png");

	@media #{$tablet}{
		background-image: url("../img/beans2.png");
		background-size: auto;
		height: 180px;
	}
	    
	h1{
		position: absolute;
	    bottom: 15%;
		color: #fff;
		font-size: 1.9rem;
		font-family: $Zilla;
		width: 100%;
		text-align: center;
	}
}

.drink {

	width: 100%;
    padding: 3%;
    max-width: 1080px;

	h2{
		text-align: center;
		align-items: center;
		flex-direction: column;
		color: $grey-color;
		font-size: 1.8rem;
		font-family: $Zilla;
	    margin: 5% 0 8% 0;

	    @media #{$tablet}{
		    margin: 0 0 8% 0;
	    }
	}

	li{
	    margin: 5%;
	}

	@media #{$tablet}{
		display: flex;
		flex-direction: row;
	}

	@media #{$desktop-small}{
		flex-direction: column;
	}

}

.drink-prices{
	font-family: $Monserrat;
    color: #7a736f;
    width: 100%;

    @media #{$tablet}{
		width: 50%;
		padding: 0 3%;
	}

	@media #{$desktop-small}{
		width: 100%;
	}
}


//Order Page Styles //


	.page-wrapper{
		
		@media #{$tablet}{
		display:flex;
		}
	}

	.products-ordering{

		@media #{$tablet}{
			width: 50%;
		    display: flex;
		    flex-direction: column;
		}

		@media #{$desktop-small}{
			width: 80%;
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
		}


	}

	.order-banner{

		background-image: url("../img/drinks.jpg");
		height: 330px;
	    background-size: cover;
	    position: relative;

	    @media #{$tablet}{
			background-image: url("../img/drink-banner.jpg");
			height: 180px;
			}

	    .order-text{

	    	font-family: $Zilla;
	    	font-size: 1.9rem;
	    	color: #fff;
	    	width: 100%;
	    	position: absolute;
	    	text-align: center;
    	    top: 70%;
    	   
	    }

	    .cart-button{

	    	position: absolute;
		    display: flex;
		    justify-content: center;
		    width: 100%;
		    top: 87%;

	    	button{
			padding: 2px 3px;
			width: 140px;
			min-width: 100px;
			border-radius: 5px;

			@media #{$desktop-small}{
			display: none;
			}
			@media #{$tablet}{
			display: none;
			}

		    }

		    img{
		    width: 20%;
		    }
	    }
	}

	.drink-list{
		background-color: $tan-color;
		flex-direction: column;
		margin: 5% 5%;
		justify-content: space-between;
		font-family: $Zilla;
		
		@media #{$desktop-small}{
			width: 40%;
		}
	}

	.drink-orderform{
		position: absolute;
	    z-index: 1;
	    top: 330px;
	    right: 0;
	    left: 0px;
	    bottom: -346%;
	    background-color:rgba(38, 38, 38, 0.6);

	    @media #{$tablet}{
		    background-color: $tan-color;
		    position: sticky;
		    border-left: 2px solid $brown-color;
			width: 50%;
		}
	}

	.your-cart{
		padding: 5%;
	    background-color: $tan-color;
	    position: relative;
	    top: 100px;
	    margin: 13%;
	    width: 70%;
	    border-radius: 18px;

	    h1{
	    	text-align: center;
		    margin-bottom: 10%;
		    font-size: 1.7rem;
		    font-family: $Zilla
	    }

	    label, p{
	    	font-family: $Monserrat;
	    	font-size: 1rem;
	    }

	    @media #{$tablet}{
			top: 0;
			margin: 8%;
			padding: 0px;
			width: 85%;
		}
	}

	.ordering{
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		margin: 0% 0 6% 0;

		input#tritemug-selection{
		    margin-left: 56px;
    		width: 40px;

    		@media #{$tablet}{
    		margin-left: 133px;
		    width: 50px;
			}
		} 

		input#espresso-selection{
		    margin-left: 64px;
    		width: 40px;

    		@media #{$tablet}{
    		margin-left: 139px;
		    width: 50px;
			}
		} 

		input#peruvian-selection{
			width: 40px;

			@media #{$tablet}{
    		margin-left: 74px;
		    width: 50px;
			}
		}
	}

	.product-info{
	    display: flex;
	    justify-content: space-between;
		margin: 4% 0 6%;
		color: $grey-color;

		h1{
			font-size: 1.3rem;
			margin-bottom: 5%;
			color: $grey-color;

		}

		h2{
			font-size: .8rem;
			color: $grey-color;
		}
	}

	.product-pricing{
	    display: flex;
	    flex-direction: row;
	    justify-content: space-between;

	    h3{
	    	font-size: 1.3rem;
	    }

		button{
		    padding: 5px 0px;
			min-width: 150px;
		    border-radius: 7px;
		    border: 2px solid #A65200;
		    position: relative;

			// @media #{$tablet}{
			// 	width: 20%;
			// }
		}
	}

	.ordering-total{
		display: flex;
	    flex-direction: row;
	    justify-content: space-between;
	    margin: 8% 0 8% 0;
	}


	.checkout-button{

		text-align: center;

		button{
			padding: 12px 0px;
			min-width: 150px;
		    border-radius: 7px;
		    border: 2px solid #A65200;
		    position: relative;
		    text-align: center;
		    width: 200px;
	   }
	}


//Footer//

footer{
	background-color: $grey-color;
    display: flex;
    flex-direction: column;
    line-height: 2rem;
    font-family: $Zilla;

    ul li{
    	text-align: center;
    }

	@media #{$tablet}{
	flex-direction: row;
	justify-content: space-evenly;
	}

    h3{
	color: $white-color;
    font-family: $Zilla;
	text-transform: uppercase;	
	margin-top: 1.5rem;
	font-size: 1.5rem;
	text-align: center;

	
    }

	.hours{
		color: $white-color;
		text-decoration: none;
		margin-bottom: .7rem;

	}

	.social{
		margin-bottom: .7rem;

		a{
		color: $white-color;
		align-items: center;
		}


	}
}


